<template>
	<div class="random-food">
		<h2 class="time">{{ currentTime }}</h2>
		<div class="container__inner">
			<h3>决定了，就是你！{{ food }}</h3>
			<button @click="startRandom" size="small" plain>
				{{ randomFoods ? '暂停' : '开始' }}
			</button>
		</div>
	</div>
</template>

<script>
import foods from '../data/foods.js';

export default {
	data() {
		return {
			foods,
			food: '',
			currentTime: '',
			btnText: '开始',
			randomFoods: false,
			randomObj: null,
			timeObj: null
		};
	},
	methods: {
		startRandom() {
			if (!this.randomFoods) {
				this.randomObj = setInterval(() => {
					this.food = this.foods[Math.floor(Math.random() * this.foods.length)];
				}, 100);
				this.randomFoods = true;
			} else {
				clearInterval(this.randomObj);
				this.randomFoods = false;
			}
		},
		setCurrentTime() {
			this.timeObj = setInterval(() => {
				this.currentTime = this.dateFormat('YYYY-mm-dd HH:MM:SS', new Date());
			}, 1000);
		},
		dateFormat(fmt, date) {
			let ret;
			const opt = {
				'Y+': date.getFullYear().toString(), // 年
				'm+': (date.getMonth() + 1).toString(), // 月
				'd+': date.getDate().toString(), // 日
				'H+': date.getHours().toString(), // 时
				'M+': date.getMinutes().toString(), // 分
				'S+': date.getSeconds().toString() // 秒
				// 有其他格式化字符需求可以继续添加，必须转化成字符串
			};
			for (let k in opt) {
				ret = new RegExp('(' + k + ')').exec(fmt);
				if (ret) {
					fmt = fmt.replace(
						ret[1],
						ret[1].length === 1 ? opt[k] : opt[k].padStart(ret[1].length, '0')
					);
				}
			}
			return fmt;
		}
	},
	mounted() {
		this.setCurrentTime();
  }
};
</script>

<style lang="scss" scoped>
.random-food {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-sizing: border-box;
  position: absolute;
  top: 40%;
	left: 51%;
	transform: translate(-50%, -50%);
	z-index: 10;
}

.random-food .time {
  color: #ffffffc7;
  text-shadow: 0.1em 0.1em 0.1em #000000;
}

.container__inner {
  padding: 1em;
  border-radius: 1em;
  color: #333;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 0.2em 0.1em rgba(0, 0, 0, 0.2);
}

.container__inner button {
  width: 100%;
  padding: 0.5em;
  border-radius: 1em;
  border: 1px solid #DCDFE6;
  background-color: #fff;
}

.container__inner button:active {
  background-color: #eee;
}

.container__inner button:focus {
  outline: none;
}
</style>
